.Home_Home {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media screen and (min-width: 1199px) {
  .Home_Home {
    padding-left: calc((100% - 1199px) / 2 + 24px);
    padding-right: calc((100% - 1199px) / 2 + 24px);
  }
}
.Home_header {
  height: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #333d47;
  width: 130vw;
  height: 230px;
}
.Home_header p {
  max-width: 780px;
  margin-top: 12px;
  font-size: 1.8rem;
  text-shadow: 0 0 4px rgba(94,96,186,0.1);
}
.Home_create-new {
  width: 75%;
  display: flex;
  flex-direction: row;
  margin: 8px auto;
  justify-content: center;
  color: #5f63b7;
  font-style: italic;
  opacity: 0.25;
  transition: opacity 0.23s;
}
.Home_create-new:hover {
  opacity: 1;
}
.Home_body {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Home_thingBox {
  width: 100%;
  max-width: 980px;
  padding: 0 24px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
.Home_loadingIndicator h3 {
  opacity: 0.5;
}
