.Navbar_Navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 24px;
  width: 100vw;
  height: 50px;
}
@media screen and (min-width: 1199px) {
  .Navbar_Navbar {
    padding-left: calc((100% - 1199px) / 2 + 24px);
    padding-right: calc((100% - 1199px) / 2 + 24px);
  }
}
.Navbar-innerPage-true {
  background: #5f63b7;
  color: #fff;
}
.Navbar-innerPage-true .Navbar_logo {
  color: #fff;
  text-shadow: 0 0 4px rgba(94,96,186,0.1);
}
.Navbar-innerPage-false {
  background: none;
  color: #333d47;
}
.Navbar-innerPage-false .Navbar_logo {
  color: #5f63b7;
}
.Navbar_logo {
  font-size: 20px;
}
.Navbar_linksBox {
  display: flex;
  flex-direction: row;
}
.Navbar_linksBox p {
  margin: 0 12px;
}
