.Input_Input {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.Input_box {
  padding: 1rem;
  background: #fff;
  border-radius: 0.4rem;
  height: 45px;
  min-height: 45px;
  max-height: 45px;
  display: inline-flex;
  flex-wrap: nowrap;
  cursor: pointer;
  border-radius: 50px;
  box-shadow: 1px 2px 8px rgba(21,76,236,0.2), 0px 1px 2px rgba(21,76,236,0.05);
  width: 100%;
  transition: all 0.23s;
}
.Input_Input label {
  font-size: 80%;
}
.Input_Input input {
  padding: 0 1rem 2px;
  font-family: 'Helvetica Neue', Helvetica, sans-serif;
  height: 100%;
  width: 100%;
  background: none;
  position: relative;
  font-weight: 500;
  color: #5f63b7;
  font-size: 1.6rem;
  letter-spacing: 0.05rem;
  line-height: 1.2;
}
.Input_Input input::-webkit-input-placeholder {
  font-family: 'Helvetica Neue', Helvetica, sans-serif;
  font-weight: 500;
  color: #959aba;
  font-size: 1.5rem;
  letter-spacing: 0.05rem;
  line-height: 1.2;
}
.Input_icon {
  margin-right: 6px;
}
