.Button_Button {
  height: 40px;
  padding: 1rem 48px;
  border-radius: 50px;
  color: #fff;
  background-color: #ff4a82;
  box-shadow: 1px 2px 8px rgba(21,76,236,0.2), 0px 1px 2px rgba(21,76,236,0.05);
  font-family: 'ProximaNova-bold', 'Helvetica Neue', Helvetica, sans-serif;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 0.02rem;
  text-decoration: none;
  transition: all 0.3s;
  font-size: 1.6rem;
  max-width: 200px;
}
.Button_Button:hover {
  background: #ff729e;
}
